.button {
  display: block;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  box-sizing: border-box;
  cursor: pointer;
  appearance: none;
  background-color: transparent; }

.button-phone {
  // padding: 14px 13px 8px 12px
  width: 180px;
  color: $white;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 5px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase; }

.button-info__mobile {
  display: none; }

.button-info {
  // padding: 17px 25px 14px 25px
  min-width: 351px;
  width: 351px;
  margin-left: 100px;
  color: $white;
  border: none;
  box-sizing: border-box;
  border-radius: 5px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: $blue; }

.button-more {
  width: 180px;
  padding: 14px 13px 14px 12px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $blue;
  border: 1px solid $blue;
  box-sizing: border-box;
  border-radius: 5px; }

@media screen and (max-width: 767px) {
  .button-info__large {
    display: none; }

  .button-info__mobile {
    margin-left: 0;
    width: 287px;
    min-width: 287px; } }
